.studentsTable-title {
  font-weight: bold;
  font-family: sans-serif;
  font-size: xx-large;
  margin-top: 10px;
  margin-bottom: 20px;

  @media (max-width: 640px) {
    text-align: center; } }

.studentsForm {
  display: flex;
  justify-content: space-between;

  .studentsForm-inputs {
    display: flex;
    flex-direction: column;
    width: 350px;

    .studentsForm-inputConteiner {
      display: flex;
      flex-direction: column;

      .studentsForm-label {
        font-weight: bold;
        font-family: sans-serif;
        font-size: 100%;
        text-align: center;
        margin-bottom: 5px; }

      .studentsForm-input {
        border: 2px solid #000000;
        border-radius: 10px;
        height: 30px;
        text-align: center;
        font-weight: bold;
        font-family: sans-serif;
        font-size: 100%;
        outline: none; }

      .studentsForm-message {
        border-radius: 10px;
        background: red;
        color: #ffffff;
        padding: 5px;
        margin-top: 5px;
        visibility: hidden; } } }

  .studentsForm-buttons {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    margin-left: 100px;

    @media (max-width: 640px) {
      margin-left: auto; }

    .studentsForm-button {
      text-align: center;
      font-weight: bold;
      font-family: sans-serif;
      font-size: 100%;
      height: 30px;
      width: 150px;
      border-radius: 10px;
      outline: none; }

    .studentsForm-button:hover {
      background-color: rgba(0,0,0,0.5); } } }

#studentsForm-filter {
  margin-top: 20px; }

.studentsTable {
  margin: 20px 0;
  text-align: center;
  font-weight: bold;
  font-family: sans-serif;
  font-size: 100%;
  border-collapse: separate;
  border-spacing: 2px;
  border: 2px solid red;

  @media (max-width: 640px) {
    border: none;
    border-spacing: 1px;
    width: 500px; }

  .head-name {
    width: 200px;
    border: 2px solid green;
    user-select: none;
    padding: 5px; }

  .result-name {
    width: 200px;
    border: 2px solid grey;
    padding: 5px; }

  .head-surname {
    width: 200px;
    border: 2px solid green;
    user-select: none;
    padding: 5px; }

  .result-syrname {
    width: 200px;
    border: 2px solid grey;
    padding: 5px; }

  .head-admission {
    width: 160px;
    border: 2px solid green;
    user-select: none;
    padding: 5px; }

  .result-admission {
    width: 160px;
    border: 2px solid grey;
    padding: 5px; }

  .head-ending {
    width: 160px;
    border: 2px solid green;
    user-select: none;
    padding: 5px; }

  .result-ending {
    width: 160px;
    border: 2px solid grey;
    padding: 5px; }

  .head-site {
    width: 200px;
    border: 2px solid green;
    user-select: none;
    padding: 5px; }

  .result-site {
    width: 200px;
    border: 2px solid grey;
    padding: 5px; }

  .head-edit {
    width: 30px;
    border: 2px solid green;
    padding: 5px; }

  .result-edit {
    width: 30px;
    border: 2px solid grey;
    user-select: none;
    padding: 5px; }

  .head-delete {
    width: 30px;
    border: 2px solid green;
    padding: 5px; }

  .result-delete {
    width: 30px;
    border: 2px solid grey;
    user-select: none;
    padding: 5px; } }
